body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-color: #b1b6ff;
  direction: rtl;
  text-align: center;
  font-family: Arial;
}

* {
  padding: 0px;
  margin: 0px;
}

#container {
  width: 100%;
  height: 100%;
  position: relative;
}

#rideme {
  margin-top: 1.5em;
  margin-bottom: 0em;
  width: 20%;
  border-bottom: 0.3em solid #b1b6ff;
}

#rideme:hover {
  border-bottom: 0.3em solid #fbca30;
}

ul {
  margin: 0 auto;
  background: #bb4400;
  width: 100%;
}

.car {
  display: none;
  position: absolute;
  left: 42%;
  bottom: 120%;
  width: 1em;
}

.li {
  /* position: relative; */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8em;
  list-style-type: none;
  background-color: #ffdca6;
  border: 0.1em solid black;
  /* margin-top: 0.5em;
  margin-right: 0.3em; */
  width: 20%;
  height: 60px;
  border-radius: 0.5em;
  -webkit-box-shadow: rgb(110, 110, 110) 10px 10px 10px;
  transition: width 1s, background-color 1s;
  -moz-transition: width 1s, background-color 1s;
  -o-transition: width 1s, background-color 1s;
}

.li:hover {
  /* width: 35%; */
  background-color: #CC5153;
}

.li a {
  text-decoration: none;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.li h1 {
  color: black;
  width: 100%;
}

.li:hover h1 {
  color: white;
}

footer {
  background-color: #ffdca6;
  transition: background-color 1s;
  -moz-transition: background-color 1s;
  -o-transition: background-color 1s;
}

footer a {
  text-decoration: none;

}

footer:hover {
  background-color: #CC5153;
  color: white;
}

footer:hover div {
  color: white;
}

.li:hover .car {
  display: block;
}

