#car-share {
  width: 30%;
  margin-bottom: 1em;
  margin-bottom: 1.5em;
}

/*search and advertising form----------------------------------------------------------------------------------*/

.search-adv-form {
  position: relative;
  display: inline-block;
  margin-top: 1em;
  margin-bottom: 1em;
  padding: 0.2em 0;
  font-size: 2em;
  font-weight: bold;
  text-align: right;
  background-color: #ccc;
  width: 50%;
  -webkit-box-shadow: rgb(110, 110, 110) 10px 10px 10px;
}

.search-adv-form p {
  font-size: 0.5em;
  margin-right: 3em;
  color: green;
}

.search-adv-form img {
  display: block;
  position: absolute;
  top: 10%;
  left: 5%;
  width: 30%;
}

#results,
#results2 {
  color: white;
  margin: 0 auto;
  margin-bottom: 0.7em;
  width: 35%;
  background-color: #C48251;
  font-size: 3em;
  font-weight: bold;
  margin-bottom: 0.9em;
}

#big-div-table {
  margin: 0 auto;
  margin-top: 0.2em;
  margin-bottom: 1.5em;
  width: 85%;
  font-size: 1.2em;
}

#big-div-table #head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #CC510A;
  border-bottom: 0.1em solid black;
  /* width: 100%; */
  padding-right: 50px;
  padding-left: 50px;
}

#big-div-table #head div {
  /* display: inline-table; */
  font-weight: bold;
  width: 20%;
  /* background: red; */
  /*border: 1px solid black;*/
}

/*-----------------change class------------------------*/

.is-closed {
  justify-content: space-between;
  align-items: 'center';
  height: 1.9em;
  font-weight: normal;
  background-color: #CC870A;
  /* line-height: 1.5em; */
  border-bottom: 0.1em solid #b1b6ff;
  /* width: 100%; */
  transition: height 1s;
  -moz-transition: height 1s;
  -webkit-transition: height 1s;
}

