  display: inline-table;
  border: 1px solid green;
  width: 13%;
  white-space: nowrap;
  -webkit-transition: color 1.5s;
  transition: color 1.5s;
  -moz-transition: color 1.5s;
  -o-transition: color 1.5s;
} */

.is-closed:hover {
  background-color: #CC5153;
}

.is-open {
  flex-direction: 'column';
  height: 11em;
  font-weight: bold;
  background-color: #CC5153;
  line-height: 1.5em;
  border-bottom: 0.1em solid #b1b6ff;
  color: white;
  transition: height 1s;
  -moz-transition: height 1s;
}

/* .divTdIsOpen div {
		display: 'flex';
		flex-direction: 'column';
		border: 1px solid green;
		width: 13%;
		white-space: nowrap;
	} */

.up {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  /* background: yellow; */
  padding-right: 50px;
  padding-left: 50px;
  /* height: 100%; */
}

.up div {
  width: 20%;
  /* background: red; */
}

Col {
  background: red;
}

.down {
  /* display: block; */
  width: 80%;
  height: 6em;
  box-shadow: 0.1em 0.3em 1em black;
  font-weight: normal;
  text-align: right;
  background-color: #e5661d;
  border: #bb4400 0.1em solid;
  margin: 0 auto;
  margin-top: 0.2em;
  color: black;
  opacity: 1;
  transition: opacity 1s;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
}

.down-opacity {
  /* display: block; */
  width: 80%;
  height: 6em;
  box-shadow: 0.1em 0.3em 1em black;
  font-weight: normal;
  text-align: right;
  background-color: #e5661d;
  border: #bb4400 0.1em solid;
  margin: 0 auto;
  margin-top: 0.2em;
  color: black;
  opacity: 0.5;
  transition: height 1s;
  -moz-transition: height 1s;
}

