.search-adv-form div label {
  width: 4em;
}

.search-adv-form div#date-time span {
  font-size: 0.7em;
}

div label {
  float: right;
  text-align: left;
  clear: right;
  margin-left: 1em;
}

#school {
  margin-left: 1em;
  white-space: nowrap;
}

div span {
  font-size: 0.8em;
}

select,
input {
  text-align: right;
}

#button-clear-search {
  font-size: 0.5em;
  margin-right: 12em;
  border-radius: 1em;
  background-color: #ffcd7f;
  padding: 0.5em;
  font-weight: bold;
}

#button-clear-search:hover {
  cursor: pointer;
  background-color: #1EC7E6;
}

#button-div-adv {
  margin-right: 5em;
}

#button-adv-itself {
  border-radius: 1em;
  background-color: #ffcd7f;
  font-weight: bold;
  text-align: center;
  font-size: 0.8em;
  padding: 0.5em;
}

#button-adv-itself:hover {
  cursor: pointer;
  background-color: #1EC7E6;
}

#button-send-button {
  margin-right: 10.5em;
}

#button-send-itself {
  border-radius: 0.5em;
  background-color: #ffcd7f;
  font-weight: bold;
  text-align: center;
  font-size: 0.8em;
  padding: 0.1em 0.5em;
}

#button-send-itself:hover {
  cursor: pointer;
  background-color: #1EC7E6;
}

#advTextarea {
  text-align: right;
  margin-top: 0.5em;
  margin-right: 2.5em;
  width: 60%;
  height: 8em;
}

/*contact form----------------------------------------------------------------------------------*/

div#instant-message {
  padding: 0.5em 0.5em;
  position: relative;
  display: inline-block;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  line-height: 1.5em;
  background-color: #ccc;
  width: 60%;
  font-family: Arial;
  font-size: 1.5em;
  text-align: right;
  -webkit-box-shadow: rgb(110, 110, 110) 10px 10px 10px;
}

#mail {
  float: left;
  font-size: 0.8em;
  font-weight: bold;
  margin-left: 0.5em;
  color: #808080;
}

#send-button {
  margin-right: 8.5em;
  color: red;
}

footer {
  width: 100%;
  height: 1em;
  border-top: 0.1em solid gray;
  position: fixed;
  bottom: 0px;
  background-color: #ffcd7f;
  color: black;
  font-size: 1.5em;
}

footer a {
  color: black;
}

/*about----------------------------------------------------------------------------------*/

div#about {
  width: 60%;
  background-color: #ccc;
  display: inline-block;
  margin-top: 2em;
  font-size: 1.5em;
  font-family: Arial;
  line-height: 1.5em;
  -webkit-box-shadow: rgb(110, 110, 110) 10px 10px 10px;
  padding: 0.5em 0.5em;
}

div#about h3 {
  text-decoration: underline;
}

.star {
  color: red;
}

#Clip-arts {
  margin-top: 1em;
}
@media screen and (max-width: 600px) {
  #rideme {
    margin-top: 1em;
    margin-bottom: 0;
    width: 50%;
  }
  h1 {
    font-size: 1.5em;
  }
  ul li {
    display: block;
    font-size: 2em;
    list-style-type: none;
    border: 3px solid black;
    width: 80%;
    margin: 0.5em auto;
  }
  .search-adv-form {
    margin-top: 0.5em;
    margin-bottom: 1.5em;
    font-size: 1.4em;
    text-align: right;
    width: 80%;
  }
  .search-adv-form div {
    margin-top: 0.3em;
    margin-bottom: 0.3em;
  }
  .search-adv-form img {
    display: block;
    position: absolute;
    top: 10%;
    left: 5%;
    width: 15%;
  }
  #button-clear-search {
    margin-right: 8em;
    padding: 0.5em;
    font-weight: bold;
  }
  #button-div-adv {
    margin-right: 4em;
  }
  #button-adv-itself {
    font-size: 0.8em;
    padding: 0.5em;
  }
  #button-send-button {
    margin-right: 0em;
  }
  #button-send-itself {
    font-size: 1em;
    padding: 0.1em 0.5em;
  }
  #big-div-table {
    width: 95%;
    font-size: 1em;
  }
  .table-div-raw {
    height: 5em;
  }
  .table-div-raw div {
    display: inline-table;
    /*border: 1px solid green;*/
    width: 13%;
    white-space: normal;
    vertical-align: middle;
  }
  .divTdIsOpen {
    height: 11.5em;
  }
  #downarrow {
    width: 1.7em;
  }
  #results,
  #results2 {
    width: 60%;
    font-size: 1.5em;
  }
  div#instant-message {
    line-height: 2em;
    margin-top: 0.5em;
    margin-bottom: 2em;
    font-size: 1.1em;
    text-align: center;
    width: 90%;
  }
  #send-button {
    margin: 0 auto;
    margin-left: 11em;
  }
  #car-share {
    width: 80%;
  }
  div#about {
    width: 80%;
    display: inline-block;
    margin-top: 1em;
    margin-bottom: 2em;
    font-size: 1em;
    font-family: Arial;
    line-height: 1.5em;
  }
  footer {
    width: 100%;
    height: 1em;
    border-top: 2px solid gray;
    position: fixed;
    bottom: 0px;
    background-color: #ffcd7f;
    color: black;
    font-size: 1em;
  }
}

@media screen and (min-width: 600px) and (max-width: 1025px) {
  #rideme {
    margin-top: 1em;
    margin-bottom: 1em;
    width: 50%;
  }
  h1 {
    font-size: 1.5em;
  }
  ul li {
    display: inline-block;
    font-size: 2em;
    list-style-type: none;
    border: 3px solid black;
    margin-right: 1em;
    width: 33%;
  }
  .search-adv-form {
    margin-top: 1em;
    margin-bottom: 1.5em;
    font-size: 1.8em;
    text-align: right;
    width: 80%;
  }
  .search-adv-form img {
    display: block;
    position: absolute;
    top: 10%;
    left: 5%;
    width: 30%;
  }
  #results,
  #results2 {
    width: 40%;
    font-size: 2em;
  }
  #big-div-table {
    width: 90%;
    font-size: 1.5em;
  }
  .table-div-raw {
    height: 5em;
  }
  .table-div-raw div {
    white-space: normal;
    vertical-align: middle;
  }
  .divTdIsOpen {
    height: 11.5em;
  }
  div#instant-message {
    margin-top: 1em;
    margin-bottom: 2em;
    font-size: 1.5em;
    text-align: right;
    width: 90%;
  }
  #car-share {
    width: 60%;
    margin-bottom: 2em;
  }
  div#about {
    width: 80%;
    display: inline-block;
    margin-top: 2em;
    margin-bottom: 2em;
    font-size: 1.3em;
    font-family: Arial;
    line-height: 1.5em;
  }
}

/*iphone 4, iphone 5*/

@media screen and (max-width: 355px) {
  .search-adv-form {
    font-size: 1em;
  }
  #results,
  #results2 {
    width: 60%;
    font-size: 1.6em;
  }
  #button-clear-search {
    font-size: 0.7em;
    margin-right: 6em;
    padding: 0.5em;
    font-weight: bold;
  }
  #button-div-adv {
    margin-right: 5em;
  }
  #button-adv-itself {
    font-size: 1em;
    padding: 0.5em;
  }
  #button-send-button {
    margin-right: 0em;
  }
  #button-send-itself {
    font-size: 1em;
    padding: 0.1em 0.5em;
  }
  .search-adv-form div {
    margin-top: 0.3em;
    margin-bottom: 0.3em;
  }
}
